/* Sign Up Pop Up Form - start */
section.SignUpFormPopUp {
	padding: 0;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: -1;
	opacity: 0;
	display: grid;
	align-content: center;
	justify-content: center;
	align-items: center;
	transition: opacity 500ms ease-in-out;

	&.active {
		opacity: 1;
		z-index: 99;
	}

	> .background {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #000;
		opacity: 0.6;
		z-index: -1;
	}

	> .outer {
		max-width: 680px;
		margin: 0 auto;
		position: relative;
		width: calc(100vw - 60px);

		> .inner {
			display: grid;
			align-content: center;
			justify-content: center;
			align-items: center;
			grid-template-columns: minmax(200px, 280px) 1fr;
			grid-template-areas: "left right";
			background-color: #000;

			.left {
				grid-area: left;
				height: 100%;
				position: relative;
				min-height: 500px;

				.background {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-position: center;
					background-size: cover;
					background-repeat: no-repeat;
				}
			}

			.right {
				grid-area: right;
				height: 100%;
				padding: 30px;
				padding-bottom: 80px;

				.closeButton {
					padding-bottom: 30px;
					text-align: right;

					button {
						all: unset;
						cursor: pointer;
					}
				}

				.content {
					h6 {
						margin: 0;
						font-family: Gza;
						color: #fff;
						font-size: 46px;
						letter-spacing: 0.01em;
						line-height: 1.1;

						span:nth-child(2) {
							font-family: "GZAOutline";
							font-weight: 400;
							font-size: 42px;
							display: block;
						}
					}

					p {
						color: #fff;
						font-family: "Univers LT Roman";
						font-size: 18px;
						line-height: 1.1;
						letter-spacing: 0.01em;
						padding: 20px 0;
					}

					.form {
						.emailContainer {
							position: relative;
							padding-bottom: 30px;

							input {
								font-family: "Gza";
								font-size: 16px;
								line-height: 1.1;
								letter-spacing: 0;
								color: #000;
								padding: 1rem 2rem;
								border-radius: 30px;
								-webkit-appearance: none;
								-moz-appearance: none;
								appearance: none;
								border: 1px solid #ffffff;
								transition: all 200ms ease;
								width: 100%;
							}

							.errorText {
								font-family: Gza;
								font-size: 12px;
								line-height: 1.1;
								letter-spacing: 0;
								color: #fff;
								padding: 0 32px;
								position: absolute;
								bottom: 25px;
								left: 0;
								display: block;
								width: 100%;
								transform: translateY(100%);
								opacity: 0;
								transition: opacity 0.2s ease;
								overflow: hidden;
							}

							&.error {
								input {
									border-color: red;
								}

								.errorText {
									opacity: 1;
								}
							}
						}

						.submit {
							font-family: "Gza";
							font-size: 18px;
							font-weight: bold;
							line-height: 1.3;
							letter-spacing: 0.05em;
							color: #fff;
							padding: 1rem 2rem;
							border-radius: 30px;
							margin: 0;
							border: none;
							background-color: #fc4f00;
							position: relative;
							opacity: 1;
							transition: opacity 200ms ease;
							width: 100%;
							cursor: pointer;

							&::after {
								content: "Signing up...";
								font-family: "Gza";
								font-size: 18px;
								font-weight: bold;
								line-height: 1.3;
								letter-spacing: 0.05em;
								color: #fff;
								padding: 1rem 2rem;
								border-radius: 30px;
								margin: 0;
								border: none;
								background-color: #fc4f00;
								position: absolute;
								opacity: 0;
								transition: opacity 200ms ease;
								top: 0;
								left: 0;
								display: block;
								width: 100%;
							}

							&.loading {
								cursor: not-allowed;
								opacity: 0.5;

								&::after {
									opacity: 1;
									cursor: not-allowed;
								}
							}
						}
					}
				}

				.confirmation {
					display: none;

					h6 {
						margin: 0;
						font-family: Gza;
						color: #fff;
						font-size: 46px;
						letter-spacing: 0.01em;
						line-height: 1.1;
						padding-bottom: 30px;
					}

					button {
						all: unset;
						font-family: Gza;
						font-size: 18px;
						font-weight: 700;
						line-height: 1.3;
						letter-spacing: 0.05em;
						color: #fff;
						padding: 1rem clamp(3rem, 5vw, 5em);
						border-radius: 30px;
						margin: 0;
						border: 1px solid #fff;
						position: relative;
						width: auto;
						cursor: pointer;
						display: inline-block;
					}
				}
			}
		}
	}

	&.submitted {
		> .outer {
			> .inner {
				.right {
					.content {
						display: none;
					}

					.confirmation {
						display: block;
					}
				}
			}
		}
	}
}

body.checkout section.SignUpFormPopUp {
	display: none !important;
}

@media (max-width: 680px) {
	section.SignUpFormPopUp {
		padding: 0 30px;

		> .outer {
			> .inner {
				grid-template-columns: 1fr;
				grid-template-areas: "right";

				.left {
					display: none;
				}

				.right {
					.content {
						h6 {
							font-size: 38px;

							span:nth-child(2) {
								font-size: 36px;
							}
						}
					}

					.confirmation {
						h6 {
							font-size: 38px;
						}
					}
				}
			}
		}
	}
}

